nav {
    background-color: rgb(185, 83, 83);
    justify-content: space-around;
    align-items: center;
    .vertical {
        width: 120px;
        height: 100vh;
        flex-direction: row;
        position: absolute;
        left: 0;
        top: 0;
        .nav-links {
            flex-direction: column;
            .nav-item a {
                display: block;
                padding: 4em 0;
                color: white;
                
                &:hover {
                    color: #abc;
                }
                &.active {
                    font-weight: bold;
                }
            }
        }
    }
}


